.root-adm {
  display: flex;
  justify-content: center;
  width: 100%;
  min-height: 50vh;
  background-repeat: no-repeat;
  background-size: cover;
  background-color: white;
  border-radius: 20px;
}

.header {
  display: flex;
  flex-direction: row;
  justify-content: center;
  margin-top: 50px;
}

.user-container {
  margin-top: 50px;

  h4 {
    margin-left: 15px;
  }
}

.switch {
  margin-top: 45px;
  display: flex;
  justify-content: center;
}
